export default{
	data(){
		return{
			goodsList:[],
			goodsPrice:0.0,//商品金额
			sumPrice:0.0,//用户支付的金额
			freight:12.0//运费
		}
	},
	onShow() {
		//本地存储的数据拉取
		uni.getStorage({
			key:"comfirmList",
			success:res=>{
				// console.log(res.data);
				this.goodsList=res.data;
				
				//循环遍历所有商品求总和
				for(let i=0;i<this.goodsList.length;i++){
					this.goodsPrice=this.goodsPrice+(this.goodsList[i].number*this.goodsList[i].price);
				}
				//用户支付的金额
				this.sumPrice=this.goodsPrice+this.freight;
			}
		})
	},
	filters:{//过滤器 防止单价乘以数量有很多小数位
		toFixed(x){
			return parseFloat(x).toFixed() 
		}
	},
	onBackPress() {
		//uni内置的方法 页面回退的时候执行的函数
		//这里页面回退 清除订单信息
		this.clearOrder();
		
	},
	methods:{
		clearOrder(){
			//清除订单的信息就是直接清除 页面上存储comfirmList的goodslist的内容
			uni.removeStorage({
				key:"conformList",
				success:(res)=>{
					this.goodsList=[]
				}
			})
		}
	}
}
